import { Commands, Controller, Inline } from '@ali/4ever-cangjie';

export default function copyLink(controller: Controller, node: Inline): Controller {
  const { selection } = controller.value;
  const range = selection
    .moveAnchorToStartOfNode(node, controller)
    .moveFocusToEndOfNode(node, controller);
  controller.command(Commands.select, range);
  return controller;
}
